@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.mobile-view{scroll-behavior:smooth;margin-top:20px;width:360px;min-height:560px;background-color:#d8d8d8;padding:20px;font-family:'Roboto', sans-serif;font-size:0.8rem}.mobile-view header{margin-top:20px;position:relative}.mobile-view header p{display:block;text-align:center;font-weight:600;letter-spacing:2px}.mobile-view header i{position:absolute}

.todo-card {
  width: 450px;
  max-width: 100%;
  background-color: rgb(255, 255, 255);

  margin-top: 20px;
  box-shadow: 0px 20px 16px rgb(0, 0, 0, 0.1);
  border-radius: 2px;
  height: 72px;
}
.todo-card .content {
  padding: 10px;
}
.todo-card .todo {
  font-weight: 600;
}
.todo-card .desc {
  color: #9e9999;
  font-size: 0.7rem;
}
.todo-card .time {
  margin-top: 4px;
  color: rgb(211, 67, 67);
  font-size: 0.7rem;
}

.todo-card .time.completed {
  color: rgb(69, 141, 69);
}
.todo-card .box-icon {
  background: rgb(222, 254, 205);
  background: linear-gradient(
    90deg,
    rgba(222, 254, 205, 1) 0%,
    rgba(167, 240, 243, 1) 100%
  );
  height: 100% !important;
  width: 76px;
  display: flex;
}
.todo-card .box-icon img {
  height: 20px;
  margin: auto;
}
.todo-card.completed {
  transform: translateX(-20px);
  width: 80%;
}

.todo-card.completed .time {
  color: rgb(69, 141, 69);
}
.todo-card .state {
  display: none;
}
.todo-card.completed .state {
  display: block;
  position: absolute;
  right: -75px;
  top: 26px;
  font-weight: 600;
  color: #575757;
}

.button {
  min-width: 40%;
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: #262626;
  font-weight: 600;
  padding: 5px 20px 5px 20px;
  cursor: pointer;
}
.button.active {
  border: 2px solid #fff;
}

.toread-card {
}
.toread-card .card-no {
  height: 46px;
  width: 46px;
  background: rgb(222, 254, 205);
  background: linear-gradient(
    90deg,
    rgba(222, 254, 205, 1) 0%,
    rgba(167, 240, 243, 1) 100%
  );
  font-weight: bold;
  text-align: center;
  line-height: 46px;
}
.toread-card .top-card {
  position: relative;
  height: 150px;
  width: 90%;
  margin-top: 20px;
  z-index: 2;
  box-shadow: 0px 20px 16px rgb(0, 0, 0, 0.1);
  border-radius: 2px;
  background-position: center !important;
  background-size: cover !important;
}
.toread-card .bottomcard {
  position: relative;
  z-index: 0;
  height: 150px;
  width: 90%;
  margin-left: auto;
  margin-top: -105px;
  background-color: #ebebeb;
  box-shadow: 0px 20px 16px rgb(0, 0, 0, 0.1);
  border-radius: 2px;
}
.toread-card .bottomcard div {
  position: absolute;
  bottom: 13px;
  left: 15px;
  display: flex;
  justify-content: space-between;
  width: 83%;
  font-weight: bold;
}

